input{
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    width: 315px;
    font-size: 20px;
    border-radius: 30px;
    border-color: aqua;
    transition: all .5s ease;
}
input :active, :focus{
    outline: 0;
    outline-offset: 0;
    border-color: blue; 
}